.buttoncontainer {
  padding: 20px 10px;
}

.buttoncontainer button {
  width: 100%;
  height: 100%;
  border: 5px outset;
  -webkit-box-shadow: -2px 2px white, -4px 4px 8px #686868;
          box-shadow: -2px 2px white, -4px 4px 8px #686868;
  -webkit-tap-highlight-color: transparent;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  position: relative;
  font-size: 2em;
  line-height: 1em;
  display: block;
  border-radius: 25px / 50%;
  text-shadow: 5px 5px 7px #686868;
  user-select: none;
}

.buttoncontainer button:active {
  right: 4px;
  bottom: -4px;
  -webkit-box-shadow: none;
          box-shadow: none;
  border-style: inset;
}

.buttoncontainer button:disabled {
  opacity: 0.8;
}
