video{-o-object-fit:contain;object-fit:contain;position:absolute;-webkit-transform:translateX(-50%);transform:translateX(-50%);width:100%;height:100%}.hostedvideo{width:100%;height:100%}.hostedvideo>*{left:0;top:0}#playpause{position:absolute;right:20px;bottom:20px;z-index:2;width:60px;height:60px;border:5px outset;-webkit-box-shadow:-2px 2px white,-4px 4px 8px #686868;box-shadow:-2px 2px white,-4px 4px 8px #686868;-webkit-tap-highlight-color:transparent;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;border:5px solid white;border-radius:50%;background-color:white;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center}#playpause:active{right:4px;bottom:-4px;-webkit-box-shadow:none;box-shadow:none;border-style:inset}#playpause:active{right:24px;bottom:16px}#playpause>img{position:relative;height:100%}#volumecontrols{position:absolute;left:20px;bottom:20px;height:60px;z-index:2;display:-webkit-box;display:-ms-flexbox;display:flex}#volumecontrols>*{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;background-color:white;border:5px solid black;position:relative;width:60px;height:60px;border:5px outset;-webkit-box-shadow:-2px 2px white,-4px 4px 8px #686868;box-shadow:-2px 2px white,-4px 4px 8px #686868;-webkit-tap-highlight-color:transparent;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}#volumecontrols>*:active{right:4px;bottom:-4px;-webkit-box-shadow:none;box-shadow:none;border-style:inset}#volumecontrols p{font-size:2em;font-weight:bold}#volumecontrols img{margin:0}#volumeup{border-radius:50% 0 0 50%;border-right-width:2.5px;z-index:3}#volumedown{border-radius:0 50% 50% 0;border-left-width:2.5px}
