#spinner{height:100px;width:100px;position:relative;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center}.antenna{position:absolute;top:0;height:50px;width:60px}.spinning{-webkit-animation:spin 0.8s ease-in-out infinite;animation:spin 0.8s ease-in-out infinite}@-webkit-keyframes spin{0%{-webkit-transform:scaleX(1);transform:scaleX(1)}20%{-webkit-transform:scaleX(0);transform:scaleX(0)}40%{-webkit-transform:scaleX(-1);transform:scaleX(-1)}60%{-webkit-transform:scaleX(0);transform:scaleX(0)}80%{-webkit-transform:scaleX(1);transform:scaleX(1)}100%{-webkit-transform:scaleX(1);transform:scaleX(1)}}@keyframes spin{0%{-webkit-transform:scaleX(1);transform:scaleX(1)}20%{-webkit-transform:scaleX(0);transform:scaleX(0)}40%{-webkit-transform:scaleX(-1);transform:scaleX(-1)}60%{-webkit-transform:scaleX(0);transform:scaleX(0)}80%{-webkit-transform:scaleX(1);transform:scaleX(1)}100%{-webkit-transform:scaleX(1);transform:scaleX(1)}}.angle{border-left:4px solid black;border-top:4px solid black;height:50%;width:50%;position:absolute;bottom:0;left:50%;-webkit-transform:rotate(10deg);transform:rotate(10deg)}.bobble{position:absolute;top:16px;right:-10px;height:10px;width:10px;border-radius:50%;background-color:white;border:4px solid black}.spinnero{position:absolute;bottom:0;height:50%;width:calc(100% - 8px);border:4px solid black;border-radius:50%;background-color:white}
